import {hot} from 'react-hot-loader/root'
import React from "react";

import Header from "./Header";
import Top from "./Top";
import Navbar from "./Navbar";
import ContentBox from "./ContentBox";
import Battle from "./Battle";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { src: "", isShow: true };
  }

  componentDidMount() {}

  handleGetData = val => {
    // console.log(val)
    this.setState({ src: val });
  };

  showPop = () => {
    this.setState({ isShow: true });
  };

  showBat = () => {
    this.setState({ isShow: false });
  };

  render() {
    const element1 = (
      <div>
        <Header>
          <Top showPop={this.showPop} showBat={this.showBat} />
          <Navbar getData={this.handleGetData} />
        </Header>
        <ContentBox getSrc={this.state.src} />
      </div>
    );
    const element2 = (
      <div>
        <Header>
          <Top showPop={this.showPop} showBat={this.showBat} />
        </Header>
        <Battle />
      </div>
    );
    return <div>{this.state.isShow ? element1 : element2}</div>;
  }
}

export default hot(App);
